import React, { Component } from 'react';
import './style.css';

class Rect extends Component {
  checkColor = () => {
    if (this.props.rect.isSorted) {
      return "green";
    } else if (this.props.rect.isSorting) {
      return "red";
    } else {
      return "black";
    }
  }

  render() {
    return (
      <div
        className='rect'
        style={{
          height: `${this.props.rect.width}px`,
          background: this.checkColor(),
          margin: this.props.marg,
          // float:'left',
          'vertical-align': 'middle'
        }}
      >
                <div className="rect-value" style={rectValueStyle}>{this.props.rect.width}</div>
      </div>
    );
  }
}

const rectValueStyle = {
    color: 'white',
    fontSize: '14px',
    'text-align': 'center',
    'margin-bottom': '100px'
  };
export default Rect;