import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import './writetous.css';

const WriteToUs = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [showSuccessMessage, setShowSuccessMessage] = useState(false);
  const history = useHistory();

  const handleSubmit = (e) => {
    e.preventDefault();
    // handle form submission here
    setShowSuccessMessage(true);
  };

  const handleGoBack = () => {
    history.push('/');
  };

  return (
    <div className="write-to-us-container">
      <h2>Write to Us</h2>
      <h3>You can feel free and drop your suggestions here</h3>
      <form onSubmit={handleSubmit} className="write-to-us-form">
        <label htmlFor="name">Name:</label>
        <input
          type="text"
          id="name"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
        <br />
        <label htmlFor="email">Email:</label>
        <input
          type="email"
          id="email"
value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <br />
        <label htmlFor="message">Message:</label>
        <textarea
          id="message"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
        />
        <br />
        <button type="submit">Send</button>
      </form>
      {showSuccessMessage && (
        <div className="success-message">
          Your message has been sent successfully!
        </div>
      )}
      <button onClick={handleGoBack} className='back-button'>Go Back to Site</button>
    </div>
  );
};

export default WriteToUs;